import React from 'react';
import s from './ActiveLine.module.css'
import {useDrag} from "@use-gesture/react";
import {useSpring, animated} from "@react-spring/web";

export const ActiveLine = () => {

    // const [{ x, y }, api] = useSpring(() => ({ x: 0, y: 0 }))


    // Set the drag hook and define component movement based on gesture data
    // const bind = useDrag(({ active, offset: [mx, my], cancel, intentional, velocity }) => {
    //   api.start({ x: active ? mx : 0, y: active ? my : 0, immediate: active })
         // if (velocity[1] >= 1 && velocity[0] >= 1){
    //     console.log(intentional)
        //     console.log(velocity)
        // }

    // })

    const [{ y }, api] = useSpring(() => ({ y: 0 }));
    const bind = useDrag(({ down, movement:[my]}) => api.start({ y: down ? my : 0 }), {
        bounds: { left: -100, right: 0, top: 0, bottom: 0 }
    })


    return (
        <div className={s.active_line}>
            <animated.div {...bind()} style={{ y }} className={s.active_line_container}>
                <div  className={s.active_line_item} />
            </animated.div>
        </div>
    );
};